<template>
<div class="d-flex align-items-start">
    <div class="nav flex-column nav-pills me-3">
        <button class="nav-link active" data-bs-toggle="pill" data-bs-target="#v-pills-statement" :value="id">Statement</button>
        <button class="nav-link" data-bs-toggle="pill" data-bs-target="#v-pills-permissions">Permissions</button>
        <button class="nav-link" data-bs-toggle="pill" data-bs-target="#v-pills-managers">Managers</button>
        <button class="nav-link" data-bs-toggle="pill" data-bs-target="#v-pills-data">Data</button>
    </div>
    <div class="tab-content container" id="v-pills-tabContent">
        <div class="tab-pane fade show active" id="v-pills-statement">Not finished yet... Please put your statement in the data zip.</div>
        <div class="tab-pane fade" id="v-pills-permissions">
            <ManageTable url="problem_permissions" :data_name="['problem_id', 'permission_id', 'permission_name']" title="Permission" name="permission"></ManageTable>
        </div>
        <div class="tab-pane fade" id="v-pills-managers">
            <ManageTable url="problem_managers" :data_name="['problem_id', 'user_id', 'user_name']" title="Manager" name="user"></ManageTable>
        </div>
        <div class="tab-pane fade" id="v-pills-data">
            <div class="row align-items-end">
                <div class="col">
                    <label for="formFile" class="form-label"><strong>Upload Data</strong></label>
                    <input class="form-control" type="file" id="formFile" accept=".zip" ref="dataFile">
                </div>
                <button class="btn btn-primary col-2 me-2" style="min-width: 80px;" @click="uploadData">Submit</button>
                <button class="btn btn-info col-2" style="min-width: 80px;">Download</button>
            </div>
            <div class="mt-3"><strong>View Data</strong></div>
            <div class="accordion mt-1" id="subtasks" v-if="problem.data.Subtasks.Record">
                <div class="accordion-item" v-for="sub in problem.data.Subtasks.Record">
                    <div class="accordion-header">
                    <button class="accordion-button collapsed py-2" style="font-size: .85rem" type="button" data-bs-toggle="collapse" :data-bs-target="'#collapse' + sub._subtaskid">
                        <div class="row" style="width:100%"><span class="col"><strong>{{sub._subtaskid}}</strong></span><span class="col me-2" style="color:green;text-align:right">Score: {{sub._score}}</span></div>
                    </button>
                    </div>
                    <div :id="'collapse' + sub._subtaskid" class="accordion-collapse collapse" data-bs-parent="#subtasks">
                        <div class="accordion-body">
                            <table class="table table-bordered">
                                <tbody>
                                    <tr v-for="(test, index) in problem.data.Tests.Record">
                                        <template v-if="test._subtaskid == sub._subtaskid">
                                            <td>Test #{{index + 1}}</td><td>{{test.input}}</td><td>{{test.output}}</td>
                                        </template>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </div>
            <div v-else>
                <table class="table table-bordered">
                    <tbody>
                        <tr v-for="(test, index) in problem.data.Tests.Record">
                            <td>Test #{{index + 1}}</td><td>{{test.input}}</td><td>{{test.output}}</td>
                        </tr>
                    </tbody>
                </table>
            </div>
            <div class="mt-2" style="text-align:right;color:green">Full Score: {{problem.data.Fullscore}}</div>
        </div>
    </div>
</div>
</template>

<script>
import ManageTable from '@/models/ManageTable.vue'
import { upload } from '@/utils'

export default {
    inject: ['reload'],
    props: ['problem', 'id'],
    components: {
        ManageTable,
    },
    methods: {
        uploadData() {
            var file = this.$refs.dataFile.files[0]
            if (!file) {
                alert("You haven't chosen any file!")
                return
            }
            if (!file.name.endsWith(".zip")) {
                alert("You should upload zip file.")
                return
            }
            var formdata = new FormData()
            formdata.append("problem_id", this.id)
            formdata.append("data", file)
            upload('problem_data', 'put', formdata, (res) => {
                this.reload()
            }, (res) => {
                alert(res.message.data._error)
            })
        },
    }
}
</script>